import { graphql, Link } from "gatsby";
import React from "react";
import Layout from "../components/Layout";
import * as styles from '../styles/product.module.css';

export default function Product({ data }) {

  // Window Undefined Handle
  if(typeof window === "undefined") {
    return;
  }

  // Language 
  const [language, setLanguage] = React.useState('');
  React.useEffect(() => {
    setLanguage(window.localStorage.getItem('CC-Language'));
  }, []);

  // Language Visibility
  React.useEffect(() => {
      document.getElementById('language').style.display = 'none';
  }, []);

  // Get data
  const product = data.contentfulProduct;

  // Change background & nav color
  React.useEffect(() => {
    document.getElementById("burger").style.backgroundColor = product.backgroundColor;
    document.getElementById("nav").style.backgroundColor = product.backgroundColor;
  });

  // Text State
  const [text, setText] = React.useState('longDescription');

  return (
    <Layout>
      <div className={styles.Product} >

        <div className={styles.textContainer}>
          <h1 className={styles.productName}>{product.name}</h1>
          <div className={styles.textButtons}>
            <button className={styles.textButton} style={{borderColor: product.backgroundColor, color: text === 'longDescription' ? product.backgroundColor : null, borderBottom: text === 'longDescription' && window.innerHeight > 800 ? "solid thick" : 'none' }} onClick={() => setText('longDescription')}>{language === "tr-TR" ? "TANIM" : "DESCRIPTION"}</button>
            <button className={styles.textButton} style={{borderColor: product.backgroundColor, color: text === 'ingredients' ? product.backgroundColor : null, borderBottom: text === 'ingredients' && window.innerHeight > 800 ? "solid thick" : 'none'  }} onClick={() => setText('ingredients')}>{language === "tr-TR" ? "İÇİNDEKİLER" : "INGREDIENTS"}</button>
            <button className={styles.textButton} style={{borderColor: product.backgroundColor, color: text === 'nutritionAdditives' ? product.backgroundColor : null, borderBottom: text === 'nutritionAdditives' && window.innerHeight > 800 ? "solid thick" : 'none'  }} onClick={() => setText('nutritionAdditives')}>{language === "tr-TR" ? "BESİN TAKVİYELERİ" : "NUTRITIONAL ADDITIVES"}</button>
            <button className={styles.textButton} style={{borderColor: product.backgroundColor, color: text === 'analysis' ? product.backgroundColor : null, borderBottom: text === 'analysis' && window.innerHeight > 800 ? "solid thick" : 'none'  }} onClick={() => setText('analysis')}>{language === "tr-TR" ? "ANALİZ" : "ANALYSIS"}</button>
            <button className={styles.textButton} style={{borderColor: product.backgroundColor, color: text === 'feedingRecommendation' ? product.backgroundColor : null, borderBottom: text === 'feedingRecommendation' && window.innerHeight > 800 ? "solid thick" : 'none'  }} onClick={() => setText('feedingRecommendation')}>{language === "tr-TR" ? "BESLEME ÖNERİSİ" : "FEEDING RECOMMENDATION"}</button>
          </div>
          {/* <p>{product.teaser}</p> */}
          <section>
            {text === 'longDescription' ? <p className={styles.texts}>{product.longDescription.longDescription}</p> : null}
            {text === 'feedingRecommendation' ? <p className={styles.texts}>{product.feedingRecommendation.feedingRecommendation}</p> : null}
            {text === 'ingredients' ? <p className={styles.texts}>{product.ingredients.ingredients}</p> : null}
            {text === 'analysis' ? <p className={styles.texts}>{product.analysis.analysis}</p> : null}
            {text === 'nutritionAdditives' ? <p className={styles.texts}>{product.nutritionAdditives.nutritionAdditives}</p> : null}
          </section>
          <Link to="/contact" className={styles.button}>{language === "tr-TR" ? "BİLGİ ALIN" : "LEARN MORE"}</Link>
        </div>

        <img src={product.packagePhoto.url} alt="product photo" className={styles.productImage}/>

        <div className={styles.imageBackground} style={{backgroundColor: product.backgroundColor}}></div>
        <div className={styles.imageBackground2} style={{backgroundColor: product.backgroundColor}}></div>
        
        <div className={styles.icons}>
          <div className={styles.icon}>
            <img src={product.icon1.url} alt="ingredients 1" className={styles.iconImage}/>
            <p className={styles.iconText}>{product.icon1Text}</p>
          </div>
          <div className={styles.icon}>
            <img src={product.icon2.url} alt="ingredients 2" className={styles.iconImage}/>
            <p className={styles.iconText}>{product.icon2Text}</p>
          </div>
          <div className={styles.icon}>
            <img src={product.icon3.url} alt="ingredients 3" className={styles.iconImage}/>
            <p className={styles.iconText}>{product.icon3Text}</p>
          </div>
          <div className={styles.icon}>
            <img src={product.icon4.url} alt="ingredients 4" className={styles.iconImage}/>
            <p className={styles.iconText}>{product.icon4Text}</p>
          </div>
          <div className={styles.icon}>
            <img src={product.icon5.url} alt="ingredients 5" className={styles.iconImage}/>
            <p className={styles.iconText}>{product.icon5Text}</p>
          </div>
          <div className={styles.icon}>
            <img src={product.icon6.url} alt="ingredients 6" className={styles.iconImage}/>
            <p className={styles.iconText}>{product.icon6Text}</p>
          </div>
          {product.icon8 !== null ? 
            <div className={styles.icon}>
              <img src={product.icon7.url} alt="ingredients 7" className={styles.iconImage}/>
              <p className={styles.iconText}>{product.icon7Text}</p>
            </div> 
          : null}
          {product.icon8 !== null ? 
            <div className={styles.icon}>
              <img src={product.icon8.url} alt="ingredients 8" className={styles.iconImage}/>
              <p className={styles.iconText}>{product.icon8Text}</p>
            </div> 
          : null}
        </div>


      </div>
    </Layout>
  )
}

export const query = graphql`
query MyQuery($slug: String) {
  contentfulProduct(slug: {eq: $slug}) {
    id
    name
    teaser
    ingredients {
      ingredients
    }
    analysis {
      analysis
    }
    feedingRecommendation {
      feedingRecommendation
    }
    nutritionAdditives {
      nutritionAdditives
    }
    longDescription {
      longDescription
    }
    slug
    packagePhoto {
      url
    }
    backgroundColor
    icon1 {
      url
    }
    icon1Text
    icon2 {
      url
    }
    icon2Text 
    icon3 {
      url
    }
    icon3Text 
    icon4 {
      url
    }
    icon4Text 
    icon5 {
      url
    }
    icon5Text 
    icon6 {
      url
    }
    icon6Text 
    icon7 {
      url
    }
    icon7Text 
    icon8 {
      url
    }
    icon8Text 
  }
}
`
;